@mixin font-size-default($size: 14) {
    $lineHeight: $size + 4;
    font-size: $size + px;
    line-height: $lineHeight + px;
}

@mixin mobile-block() {
    @media screen and (max-width: 768px) {
        width: 100%;
    }
}

@mixin multi-line ($num) {
    word-break: break-all;
    display: -webkit-box;
    -webkit-line-clamp: $num;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

@mixin single-line {
    overflow: hidden;
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    -webkit-text-overflow: ellipsis;
    -moz-text-overflow: ellipsis;
    white-space: nowrap;
}
.embed {
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    border: 2px solid #28282a;
    color: #fff;
    border-radius: 4px;

    &-header {
        padding: 4px;
        background-color: #28282a;
        font-size: 16px;
        display: flex;
        justify-content: flex-end;
        &-logo {
            cursor: pointer;
            
        }
    }
    
    &-iframe {
        flex: 1;
        overflow: hidden;
    }

    &-footer {
        padding: 4px;
        background-color: #28282a;
        display: flex;
        justify-content: flex-end;
        > button {
            background-color: #424242;
            color: #fff;
            border: none;
            padding:  4px;
            border-radius: 4px;
            cursor: pointer;
        }
    }
}